import{Login} from "@/api/index"
import{getLang,getValue,setValue} from "@/utils/pub"
export default {
  name: 'App',
   components: {
  
  },
  data() {
    return { 
     list:["wer","wer","32","32","32","32","3243","43","45","54","65","65","65","wer","32","32","32","32","3243","43","45","54","65","65","65"],
    }
  },
    created(){
      setValue("frompath","/")
    if(window.location.host.indexOf(":")>0){
    
        var ip= window.location.host.split(":")[0]
         var port= window.location.host.split(":")[1]
          if ( ip!=null&& ip!=undefined&&ip!='null'){
            setValue('ip', ip);
            setValue('port', port);
         }
        }
        },
   methods: {
          getlan(name){ var str=this.$t('lang.'+name+''); if(str.indexOf("lang.")>=0){ return name; }return str },
    scrollHanle(evt) {
      console.log(evt)
    },
checkDevice() {
        let ua = navigator.userAgent.toLowerCase();
        //Android终端
        let isAndroid = ua.indexOf('Android') > -1 || ua.indexOf('Adr') > -1;  
　   //Ios终端
        let isiOS = !!ua.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); 
        if (isWeixinBrowser()) {
           this.$router.push({name: '/invoice' })
        } else {
           if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
               this.$router.push({name: '/invoice' })
           } else if (/(Android)/i.test(navigator.userAgent)) {
              //Android终端
                this.$router.push({name: '/invoice' })
           }
        }

        function isWeixinBrowser() {
            return (/micromessenger/.test(ua)) ? true : false;
        }
  },
  isMobile() {
	 let flag = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
	 return flag;
},
  changeLangEvent: function () {
    console.log('changeLangEvent');
    this.$confirm('Change language?', 'Alert', {
      confirmButtonText: 'OK',
      cancelButtonText: 'Cancel',
      type: 'warning'
    }).then(() => {
      if ( this.$i18n.locale === 'zh-CN' ) {
        this.$i18n.locale = 'en-US';//关键语句  2.23  3.5  3.8  4.5  4.25  5.16
        console.log('en-US')
      }else {
        this.$i18n.locale = 'zh-CN';//关键语句
        console.log('zh-CN')
      }
    }).catch(() => {
      console.log('catch');
      this.$message({
        type: 'info',
      });
    });
  }

}
}


</script>

